export const initiatePwaButton = (buttonId = "addBtn") => {
  // PWA install promotion banner on start
  let deferredPrompt: any = null;
  const addBtn: any = document.getElementById(buttonId);
  window.addEventListener("beforeinstallprompt", (e: any) => {
    // Prevent Chrome 67 and earlier from automatically showing the prompt
    e.preventDefault();
    deferredPrompt = e;
    // Update UI to notify the user they can add to home screen
    addBtn.style.display = "block";

    addBtn.addEventListener("click", () => {
      // hide our user interface that shows our A2HS button
      addBtn.style.display = "none";
      deferredPrompt.prompt();
      // Wait for the user to respond to the prompt
      deferredPrompt.userChoice.then((choiceResult: any) => {
        if (choiceResult.outcome === "accepted") {
          console.log("User accepted the A2HS prompt");
          addBtn.style.display = "none";
        } else {
          console.log("User dismissed the A2HS prompt");
        }
        deferredPrompt = null;
      });
    });

    if (window.matchMedia("(display-mode: fullscreen)").matches) {
      addBtn.style.display = "none";
    }
  });
};

// Requests continuous read/write access to a file and returns an object to let you write to the file on demand
export const requestFileAccess = () => {
  window.requestFileSystem =
    window.requestFileSystem || window.webkitRequestFileSystem;

  // Handle errors
  function errorHandler(e: any) {
    console.log(e);
    var msg = "";

    switch (e.code) {
      case 10:
        msg = "QUOTA_EXCEEDED_ERR";
        break;
      case 1:
        msg = "NOT_FOUND_ERR";
        break;
      case 2:
        msg = "SECURITY_ERR";
        break;
      case 9:
        msg = "INVALID_MODIFICATION_ERR";
        break;
      case 7:
        msg = "INVALID_STATE_ERR";
        break;
      default:
        msg = "Unknown Error";
        break;
    }
    console.log("Error: " + msg);
  }

  // Init and write some data to a file
  function onInitFs(fs: any) {
    fs.root.getFile(
      "log-f-api.txt",
      { create: true },
      function (fileEntry: any) {
        // fileEntry.isFile === true;
        // fileEntry.name == "log-f-api.txt";
        // fileEntry.fullPath == "/log-f-api.txt";
        // Create a FileWriter object for our FileEntry (log.txt).
        fileEntry.createWriter(function (fileWriter: any) {
          fileWriter.onwriteend = function (e: any) {
            console.log("Write completed.", e);
          };

          fileWriter.onerror = function (e: any) {
            console.log("Write failed: ", e);
          };

          const ourData = new Blob(["put some nice text in our file...."], {
            type: "text/plain",
          });
          console.log("blob", ourData);
          fileWriter.write(ourData);
          // Create a new Blob and write it to log.txt.
          //@ts-ignore
          // const BlobBuilder = window.WebKitBlobBuilder || window.MozBlobBuilder;
          // var bb = new BlobBuilder();
          // bb.append("some stuff");
          // console.log("bb size:" + bb.size);
          // bb.append("put some nice text in our file....");
          // var ourData = bb.getBlob("text/plain");
          // fileWriter.write(ourData);
        }, errorHandler);
      },
      errorHandler
    );
  }
  window.requestFileSystem(
    window.PERSISTENT,
    5 * 1024 * 1024,
    onInitFs,
    errorHandler
  );
};
